.GeneralBanner {
    position: relative;
    img {
        height: 170px;
        width: 100%;
    }
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, .1);
    }
}