.ProductImage {
    padding: 35px 65px;
    box-sizing: border-box;

    @media only screen and (min-width: 1700px) {
        padding: 35px 5vw;
    }

    img {
        width: 100%;
    }

    @media screen and (max-width: 767px) {
        padding-right: 5px;
        padding-left: 5px;
    }
}